<template>
    <view class="page bgfb">
        <view style="margin-left: 20px;padding-top: 30px;">{{ $t('小时') }}</view>
        <view style="padding-top: 0upx">
            <qiun-data-charts type="column" :opts="opts" :chartData="chartData" :ontouch="true" />
        </view>

        <view class="stoptitle">{{ $t('停机报表分析') }}</view>

        <view class="stopList">
            <view class="ch-flex">
                <view class="flex-item" v-for="item in detailData" :key="item.id">
                    {{ item.reason }}：<text>{{ item.duration }}</text>{{ $t('小时') }}</view>
            </view>
        </view>

    </view>
</template>

<script>
import Api from "@/api/stop/index.js"
export default {
    data() {
        return {
            chartData: {},
            isGet: true,
            detailData: {},
            opts: {
                color: ["#f19ec2", "#1890FF", "#91CB74", "#FAC858", "#EE6666", "#73C0DE", "#3CA272", "#FC8452", "#9A60B4", "#ea7ccc"],
                padding: [15, 15, 0, 5],
                rotate: false,
                touchMoveLimit: 24,
                enableScroll: true,
                legend: { show: false },
                xAxis: {
                    disableGrid: true,
                    scrollShow: true,
                    itemCount: 5
                },
                yAxis: {
                    data: [
                        {
                            min: 0,
                        }
                    ]
                },
                extra: {
                    column: {
                        type: "group",
                        width: 20,
                        activeBgColor: "#000000",
                        activeBgOpacity: 0.08
                    }
                }
            },
        };
    },

    onLoad(options) {
        uni.$u.http.get(Api.detail + "machineNo=" + options.machineNo).then(result => {
            console.log('result', result);
            if (result.code === 200) {
                if (result.data != []) {
                    this.detailData = result.data
                    this.getServerData();
                }
            }
        }).catch(er => {
            console.log('er', er);
        }).finally(() => {
            this.isGet = false
        })
    },
    onShow() {
        uni.setNavigationBarTitle({ // 修改头部标题
            title: this.$t("stop.report.detail")
        });
    },
    methods: {
        getServerData() {
            var titleList = []
            var seriesList = []
            this.detailData.forEach(item => {
                titleList.push(item.reason)

                const map = new Map()
                map.set('name', '时长')
                map.set('duration', item.duration)
                seriesList.push(map)
            });

            if (titleList != [] && seriesList != []) {
                setTimeout(() => {
                    let res = {
                        categories: titleList,
                        series: [
                            {
                                name: '时长',
                                data: seriesList.map((item) => item.get('duration'))
                            },
                        ]
                    };
                    this.chartData = JSON.parse(JSON.stringify(res));
                }, 500);
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.bgfb {
    background-color: #fbfbfb;
}

.stoptitle {
    margin: 40upx 30upx 0;
    color: $ch-theme;
    padding-bottom: 20upx;
    font-size: 30upx;
    @include after($c: $ch-theme)
}

.stopList {
    margin: 40upx 30upx 0;

    .ch-flex {
        flex-wrap: wrap;
    }

    .flex-item {
        width: 50%;
        font-size: 28upx;
        color: #333;
        margin-bottom: 12upx;

        text {
            color: $ch-theme;
        }
    }
}
</style>
